'use client';

import styles from './listing.module.css';
import Image from 'next/image';
import React, { useEffect, useState } from 'react';
import Link from 'next/link';

// Importe suas imagens
import logo1 from '@/images/fotos/logotipo_1.gif';
import logo2 from '@/images/fotos/logotipo_2.gif';
import logo3 from '@/images/fotos/logotipo_3.gif';
import logo4 from '@/images/fotos/logotipo_4.gif';
import logo5 from '@/images/fotos/logotipo_5.gif';
import logo6 from '@/images/fotos/logotipo_6.gif';
import logo7 from '@/images/fotos/logotipo_7.gif';
import logo8 from '@/images/fotos/logotipo_8.gif';

interface Servico {
    titulo: string;  // Título do serviço (adicionado)
    nome: string;    // Nome do freelancer
    preco: number;   // Preço do serviço
}

function Listing() {
    const [servicos, setServicos] = useState<Servico[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        const fetchServicos = async () => {
            try {
                const response = await fetch('http://localhost/dashboard/api-designOdyssey/servicos/index.php');
                
                if (!response.ok) {
                    throw new Error('Erro ao buscar serviços');
                }

                const data = await response.json();
                setServicos(data);
            } catch (error) {
                console.error('Falha ao buscar serviços:', error);
                setError('Não foi possível carregar os serviços. Tente novamente mais tarde.');
            } finally {
                setLoading(false);
            }
        };

        fetchServicos();
    }, []);

    if (loading) {
        return <div className={styles.main}>Carregando serviços...</div>;
    }

    if (error) {
        return <div className={styles.main}>{error}</div>;
    }

    // Mapeamento de imagens
    const logos = [logo1, logo2, logo3, logo4, logo5, logo6, logo7, logo8];

    return (
        <div className={styles.main}>
            <div className={styles.top}>
                <h1>Design de Logotipo</h1>
                <p>Deixe-nos ajudar a achar o serviço de Designer certo para você!</p>
                <div className={styles.botoesTopMain}>
                    <div className={styles.botaoTop}>
                        <span>Design de Site</span>
                    </div>
                    <div className={styles.botaoTop}>
                        <span>Design Gráfico</span>
                    </div>
                    <div className={styles.botaoTop}>
                        <span>Design Logotipo</span>
                    </div>
                    <div className={styles.botaoTop}>
                        <span>Arte Digital</span>
                    </div>
                </div>
            </div>
            <div className={styles.contentMain}>
                <div className={styles.contentBoxes}>
                    {servicos.map((servico, index) => (
                        <Link href='/gig_page' className={styles.contentBox} key={index}>
                            <Image 
                                src={logos[index % logos.length]} 
                                height={300} 
                                width={300} 
                                alt={`logo-${index}`}
                            />
                            <h1>{servico.titulo}</h1>  {/* Título do serviço */}
                            <p>Anúncio de {servico.nome}</p>
                            <span>A partir de R${servico.preco.toFixed(2)}</span>
                        </Link>
                    ))}
                </div>
            </div>
        </div>
    );
}

export default Listing;8080